.transactionHistory{
    width: 550px;
    text-align: center;
    margin: 10px auto;
    border: 3px solid rgb(0, 119, 255);
    
}
.thead{
    background-color: darkorchid;
    color: rgb(23, 199, 111);
    text-transform: uppercase;
    height: 50px;
    font-size: 22px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    
}
tr:nth-child(odd) {
    background-color: red;
}
.tbody{
    /* display: block; */
    text-transform: uppercase;
    height: 50px;
    font-size: 14px;
    font-style: oblique;
    }

    .type{
    
        display: flex;
        justify-content: center;
    }

    .gray{
        background-color: rgb(207, 31, 31);
        }
.white{
    background-color: rgb(233, 216, 216);
